import React from 'react'
import quotes from "../QuotesDatabase"
import QuoteAndAuthor from "../QuoteAndAuthor"

class qou extends React.Component {
  constructor() {
    super();
    this.state = {
      quote: quotes[Math.floor(Math.random() * quotes.length)].quote,
      author: quotes[Math.floor(Math.random() * quotes.length)].author,
    };
  }
  randomQuote() {
    const randomNumber = Math.floor(Math.random() * quotes.length);
    return quotes[randomNumber];
    
  }
  shuffleQuotes(array){
    return array.sort(()=>Math.random()-0.5)
  }


  handleClick = () => {
    const generateRandomQuote = this.randomQuote();
    this.setState({
      quote: generateRandomQuote.quote,
      author: generateRandomQuote.author
    });
    this.shuffleQuotes(quotes)
  };

       componentDidMount() {
       this.getQuote();
       this.interval = setInterval(() => {
         this.getQuote();
       },4000);
     }

          getQuote() {
           this.handleClick()
     }
  

  render() {
    return    <QuoteAndAuthor handleClick={this.handleClick} {...this.state} />
     
    
  }
}

export default qou;