import React from 'react'
import {useHistory} from 'react-router-dom';

 
 
 function Description(props) {
    let history = useHistory();

     return (
         
         <div className="ibdb-description">
             <h1 className="ibdb-logo" style={{cursor :'pointer'}} onClick={()=> history.push('/ohadl-books')} >Ibdb</h1>
             
             
            
             <div className="description-body">
                
             <h1 className="name-and-by">{props.bookList[props.descriptionPlace].name} By : {props.bookList[props.descriptionPlace].by}</h1>
             <img className='imgDescription' src = {props.bookList[props.descriptionPlace].img} />
             <br></br>
             <br></br>
             <p className="link-to-amazon" >{props.bookList[props.descriptionPlace].cart}</p>
             <div className='imgDescription-container' >
                  <div className="descriptin-flex" >
                  {props.bookList[props.descriptionPlace].description}
                   <h2 className="rating" >{props.bookList[props.descriptionPlace].rating}</h2>
                   <img className='imgBy' src = {props.bookList[props.descriptionPlace].imgBy} />
                   <h2 className="name-of-author" >{props.bookList[props.descriptionPlace].aboutHeader}</h2>
                   {props.bookList[props.descriptionPlace].about}
                  </div>
             </div>
            
             </div>
            
                
      
         </div>
     )
 }
 
 export default Description
 