import React from "react";
import {useHistory} from 'react-router-dom';

function ContactList(props){
    let history = useHistory();

    return (
        <div className="map-books">
            {
                
          props.bookList.map((p,index)=>
          
          
           (
            
               <div className='card' style={{cursor :'pointer'}} key={`cn-${index}`} onClick={()=>{
                let temp = p.bookListIndex
                   props.SetState({
                       ...props.state,
                       descriptionPlace: temp,
                       
                       
                   }) 
                   
                   history.push('/description')
                    }} >

                      <img src={p.img} />
                      <span className="span-name">{p.name}</span>
                      <p>Auother: {p.by}</p>
                      
   
                            </div>
                    )
                )
            }
        </div>
    )
}
export default ContactList;