    name: 'Tristram Shandy',
    description: 'As its title suggests, the book is ostensibly Tristrams narration of his life story. But it is one of the central jokes of the novel that he cannot explain anything simply, that he must make explanatory diversions to add context and colour to his tale, to the extent that we do not even reach Tristrams own birth until Volume III.',
    by: 'Laurence Sterne',
    rating : 'The 47th greatest fiction book of all time',
    aboutHeader: 'Laurence Sterne',
    about: 'Laurence Sterne was an Irish-born English novelist and an Anglican clergyman. He is best known for his novels The Life and Opinions of Tristram Shandy, Gentleman, and A Sentimental Journey Through France and Italy; but he also published many sermons, wrote memoirs, and was involved in local politics. Sterne died in London after years of fighting consumption (tuberculosis).',
