    name: 'Pale Fire',
    description: 'The novel is presented as a poem titled "Pale Fire" with commentary by a friend of the poets. Together these elements form two story lines in which both authors are central characters. The interaction between Kinbote and Shade takes place in the fictitious small college town of New Wye, Appalachia, where they live across a lane from each other, from February to July, 1959. Kinbote writes his commentary from then to October, 1959, in a tourist cabin in the equally fictitious western town of Cedarn, Utana. Both authors recount many earlier events, Shade mostly in New Wye and Kinbote mostly in Europe, especially the "distant northern land" of Zembla.',
