    name: 'The Idiot',
    description: 'The Idiot is a novel written by the Russian author Fyodor Dostoyevsky and first published in 1868. It was first published serially in Russian in Russky Vestnik, St. Petersburg, 1868-1869. The Idiot is ranked, along with other works from Dostoevsky, as one of the most brilliant literary achievements of the Russian "Golden Age" of Literature. It would not be translated into English until the twentieth-century',
    by: 'Fyodor Dostoyevsky',
    rating : 'The 61st greatest fiction book of all time',
    aboutHeader: 'Fyodor Dostoyevsky',
    about: 'Fyodor Mikhailovich Dostoevsky was born in Moscow in 1821. His debut, the epistolary novella Poor Folk (1846), made his name. In 1849 he was arrested for involvement with the politically subversive Petrashevsky circle and until 1854 he lived in a convict prison in Omsk, Siberia. From this experience came The House of the Dead (1860-2). In 1860 he began the journal Vremya (Time). Already married, he fell in love with one of his contributors, Appollinaria Suslova, eighteen years his junior, and developed a ruinous passion for roulette. After the death of his first wife, Maria, in 1864, Dostoyevsky completed Notes from Underground and began work towards Crime and Punishment (1866). The major novels of his late period are The Idiot (1868), Demons (1871-2) and The Brothers Karamazov (1879-80). He died in 1881.',
