    name: 'As I Lay Dying',
    description: 'The book is told in stream of consciousness writing style by 15 different narrators in 59 chapters. It is the story of the death of Addie Bundren and her familys questnoble or selfishto honor her wish to be buried in the town of Jefferson. The novel was written in six weeks while Faulkner was working at a power plant, published in 1930, and described by Faulkner as a "tour de force".',
