    description: 'Taking place mostly in rural Georgia, the story focuses on female black life during the 1930s in the Southern United States, addressing the numerous issues including their exceedingly low position in American social culture. The novel has been the frequent target of censors and appears on the American Library Association list of the 100 Most Frequently Challenged Books of 1990-1999 at number seventeen because of the sometimes explicit content, particularly in terms of violence.',
