    about: 'Henri-Marie Beyle, better known by his pen name Stendhal , was a 19th-century French writer. Known for his acute analysis of his characters psychology, he is considered one of the earliest and foremost practitioners of realism in his two novels Le Rouge et le Noir (The Red and the Black, 1830) and La Chartreuse de Parme (The Charterhouse of Parma, 1839).',
