  name: 'The Lives of the Artists',
  description: 'Packed with facts, attributions, and entertaining anecdotes about his contemporaries, Giorgio Vasar\'s collection of biographical accounts also presents a highly influential theory of the development of Renaissance art. Beginning with Cimabue and Giotto, who represent the infancy of art, Vasari considers the period of youthful vigour, shaped by Donatello, Brunelleschi, Ghiberti, and Masaccio, before discussing the mature period of perfection, dominated by the titanic figures of Leonardo, Raphael, and Michelangelo. This specially commissioned translation contains thirty-six of the most important lives as well as an introduction and explanatory notes. About the Series: For over 100 years Oxford World\'s Classics has made available the widest range of literature from around the globe. Each affordable volume reflects Oxford\'s commitment to scholarship, providing the most accurate text plus a wealth of other valuable features, including expert introductions by leading authorities, helpful notes to clarify the text, up-to-date bibliographies for further study, and much more.' ,
