  rating : '',
  aboutHeader: 'E.B. White, Garth Williams',
  about: 'Elwyn Brooks White was a leading American essayist, author, humorist, poet and literary stylist and author of such beloved children\'s classics as Charlotte\'s Web, Stuart Little, and The Trumpet of the Swan. He graduated from Cornell University in 1921 and, five or six years later, joined the staff of The New Yorker magazine. He authored over seventeen books of prose and poetry and was elected to the American Academy of Arts and Letters in 1973. White always said that he found writing difficult and bad for one\'s disposition. Mr. White has won countless awards, including the 1971 National Medal for Literature and the Laura Ingalls Wilder Medal, which commended him for making a substantial and lasting contribution to literature for children.',
